@media (max-width: 500px) {
  .hey {
    flex-direction: column;
  }
}
.property-card{
 
  padding: 100px;
  margin-right: 20px;
  margin-left: 60px;
  margin-bottom: 25px;
  float: none;
  position: relative;
  background-color: whitesmoke;
  width: 320px;
  padding: 10px;
  max-width: 85vw;
  height:55vh;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0.3);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}


.property-card span{
  margin-left: 20px;
  position: absolute;
  bottom:0;
  margin-bottom: 50px;
  color: black;
  
}

.property-card p{
  margin-left: 20px;
  position: absolute;
  bottom:0;
  margin-bottom: 30px; 
}

.property-card strong{
  margin-left: 20px;
  position: absolute;
  bottom:0;
  margin-bottom: 10px;
 
}

.checkout{
  background-color: rgba(18, 101, 241, 0.07);
}

.checkout-background{
  background-color: #eee;
}
.checkout-input{
  width: 55px;
}